.form {
    display: block;
    box-sizing: border-box;
    padding: 44px 50px 48px 50px;
    background-color: $tangaroa;

    @include mobile {
        padding: 35px 30px 35px 30px;
    }
}

.form__header {
    padding: 0;
    margin: 0 auto;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    color: $alice-blue;
    margin-bottom: 26px;
    letter-spacing: 0.01em;

    & span {
        @include mobile {
            display: block;
        }
    }
    
    @include mobile {
        width: 214px;
        text-align: center;
        line-height: 25px;
        margin-bottom: 27px;
    }
}

.form__field-list {
    padding: 0;
    margin: 0;
    list-style: none;
}

.form__field-item:not(:last-child) {
    margin-bottom: 20px;
}

.form__field-item--checkbox {
    position: relative;
    padding: 0 0 0 23px;
    margin-bottom: 28px;
}

.form__input {
    box-sizing: border-box;
    width: 100%;
    border: none;
    border-radius: none;
    padding: 14px 16px 13px 16px;

    &::placeholder {
        font-size: 14px;
        font-weight: 400;
        line-height: 21px;
        color: $tangaroa;
    }

    &:focus,
    &:active {
        outline: none;
        border: none;
    }
}


.form__checkbox-label {
    font-size: 10px;
    font-weight: 400;
    line-height: 13px;
    color: $alice-blue;

    @include mobile {
        display: block;
        font-size: 10px;
        line-height: 10px;
        padding-left: 5px;
    }

    &::before {
        content: "";
        position: absolute;
        width: 18px;
        height: 18px;
        top: 0;
        left: 0;
        background-color: $alice-blue;
    }
}


.form__checkbox-input:checked + .form__checkbox-label::before {
    background: url("../../img/sprite/mark-form.svg") center no-repeat;
    background-color: $alice-blue;
}